/************************************* shooping cart / containers *********************************************/

.cart-container{
	  width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    text-align: center;
    align-items: center;
    justify-content: center;
}

.cart-title{
    width: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding-bottom: 20px;
    color: rgb(111, 2, 63);
}

.cart-btn-container{
    width: 100%;
    text-align: center;
    align-items: center;
    justify-content: center;
    padding-top: 20px;
}

/***************************************** shooping cart / table **************************************************/

.cart-table{
    padding: 10px;
    border-collapse: collapse;
}

.cart-table > tbody > tr{
    border-bottom: 1px solid rgb(207, 207, 207);
}

.cart-table > tbody > tr > td{
    width: auto;
}

.shooping-cart-item-img{
    width: 70px;
    height: auto;
}

.row-total{
  height: 70px;
}

.btn-delete-cart-item{
    width: 30px;
    height: 30px;
    opacity: 0.8;
    transition: all 0.3s;
}

.btn-delete-cart-item:hover{
    width: 40px;
    height: 40px;
    opacity: 1;
}

.btn-delete-cart-item:active{
    opacity: 0.5;
}

/************************************************ shooping cart / buttons *****************************************/

.btn-shopping-cart{
    width: auto;
    height: 50px;
    text-align: center;
    font-size: medium;
    margin: 10px;
    padding: 0 15px;
    background-color: rgb(111, 2, 63);
    color: white;
    border: none;
    border-radius: 4px;
}

.btn-shopping-cart:hover{
    background-color: rgb(85, 7, 51);
    cursor: pointer;
}

.btn-shopping-cart:active{
    background-color: rgb(56, 1, 32);
    cursor: pointer;
}

