/****************************************************** Shine ***********************************************/
.divider{
    width: 100%;
    height: 20px;
}

.divider > hr {
    border: 0;
    max-width: 100%;
    background-position: 50%;
    box-sizing: border-box;
  }

.shine {
    height: 20px;
    width: 100%;
    background-image: radial-gradient(
        farthest-side at 50% -50%,
        hsla(0, 0%, 100%, 0.5),
        hsla(0, 0%, 100%, 0)
    );
    position: relative;
    top: 0;
    margin: 0;
}

.shine::before {
    height: 1px;
    position: absolute;
    top: -1px;
    left: 0;
    right: 0;
    background-image: linear-gradient(
        90deg,
        hsla(0, 0%, 100%, 0),
        hsla(0, 0%, 100%, 0.75) 50%,
        hsla(0, 0%, 100%, 0)
    );
}

